.flex {
	display: flex;
}

.justify_start {
	justify-content: flex-start;
}

.justify_end {
	justify-content: flex-end;
}

.justify_around {
	justify-content: space-around;
}

.justify_between {
	justify-content: space-between;
}

.justify_center {
	justify-content: center;
}

.align_center {
	align-items: center;
}

.align_start {
	align-items: start;
}

.align_end {
	align-items: end;
}

.direction_row {
	flex-direction: row;
}

.direction_column {
	flex-direction: column;
}

.gap_4 {
	gap: 4px;
}

.gap_8 {
	gap: 8px;
}

.gap_16 {
	gap: 16px;
}

.gap_32 {
	gap: 32px;
}
